import Vue from 'vue';
import i18n from '../../../i18n';
import MkDriveWindow from './drive-window.vue';
import XNavFolder from './drive.nav-folder.vue';
import XFolder from './drive.folder.vue';
import XFile from './drive.file.vue';
import contains from '../../../common/scripts/contains';
import { url } from '../../../config';
import { faCloudUploadAlt } from '@fortawesome/free-solid-svg-icons';
export default Vue.extend({
    i18n: i18n('desktop/views/components/drive.vue'),
    components: {
        XNavFolder,
        XFolder,
        XFile
    },
    props: {
        initFolder: {
            type: Object,
            required: false
        },
        type: {
            type: String,
            required: false,
            default: undefined
        },
        multiple: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            /**
             * ()
             * * null 
             */
            folder: null,
            files: [],
            folders: [],
            moreFiles: false,
            moreFolders: false,
            hierarchyFolders: [],
            selectedFiles: [],
            uploadings: [],
            connection: null,
            /**
             * 
             */
            draghover: false,
            /**
             * 
             * ()
             */
            isDragSource: false,
            fetching: true
        };
    },
    mounted() {
        this.connection = this.$root.stream.useSharedConnection('drive');
        this.connection.on('fileCreated', this.onStreamDriveFileCreated);
        this.connection.on('fileUpdated', this.onStreamDriveFileUpdated);
        this.connection.on('fileDeleted', this.onStreamDriveFileDeleted);
        this.connection.on('folderCreated', this.onStreamDriveFolderCreated);
        this.connection.on('folderUpdated', this.onStreamDriveFolderUpdated);
        this.connection.on('folderDeleted', this.onStreamDriveFolderDeleted);
        if (this.initFolder) {
            this.move(this.initFolder);
        }
        else {
            this.fetch();
        }
    },
    beforeDestroy() {
        this.connection.dispose();
    },
    methods: {
        onContextmenu(e) {
            this.$contextmenu(e, [{
                    type: 'item',
                    text: this.$t('contextmenu.create-folder'),
                    icon: ['far', 'folder'],
                    action: this.createFolder
                }, {
                    type: 'item',
                    text: this.$t('contextmenu.upload'),
                    icon: 'upload',
                    action: this.selectLocalFile
                }, {
                    type: 'item',
                    text: this.$t('contextmenu.url-upload'),
                    icon: faCloudUploadAlt,
                    action: this.urlUpload
                }]);
        },
        onStreamDriveFileCreated(file) {
            this.addFile(file, true);
        },
        onStreamDriveFileUpdated(file) {
            const current = this.folder ? this.folder.id : null;
            if (current != file.folderId) {
                this.removeFile(file);
            }
            else {
                this.addFile(file, true);
            }
        },
        onStreamDriveFileDeleted(fileId) {
            this.removeFile(fileId);
        },
        onStreamDriveFolderCreated(folder) {
            this.addFolder(folder, true);
        },
        onStreamDriveFolderUpdated(folder) {
            const current = this.folder ? this.folder.id : null;
            if (current != folder.parentId) {
                this.removeFolder(folder);
            }
            else {
                this.addFolder(folder, true);
            }
        },
        onStreamDriveFolderDeleted(folderId) {
            this.removeFolder(folderId);
        },
        onChangeUploaderUploads(uploads) {
            this.uploadings = uploads;
        },
        onUploaderUploaded(file) {
            this.addFile(file, true);
        },
        onMousedown(e) {
            if (contains(this.$refs.foldersContainer, e.target) || contains(this.$refs.filesContainer, e.target))
                return true;
            const main = this.$refs.main;
            const selection = this.$refs.selection;
            const rect = main.getBoundingClientRect();
            const left = e.pageX + main.scrollLeft - rect.left - window.pageXOffset;
            const top = e.pageY + main.scrollTop - rect.top - window.pageYOffset;
            const move = e => {
                selection.style.display = 'block';
                const cursorX = e.pageX + main.scrollLeft - rect.left - window.pageXOffset;
                const cursorY = e.pageY + main.scrollTop - rect.top - window.pageYOffset;
                const w = cursorX - left;
                const h = cursorY - top;
                if (w > 0) {
                    selection.style.width = w + 'px';
                    selection.style.left = left + 'px';
                }
                else {
                    selection.style.width = -w + 'px';
                    selection.style.left = cursorX + 'px';
                }
                if (h > 0) {
                    selection.style.height = h + 'px';
                    selection.style.top = top + 'px';
                }
                else {
                    selection.style.height = -h + 'px';
                    selection.style.top = cursorY + 'px';
                }
            };
            const up = e => {
                document.documentElement.removeEventListener('mousemove', move);
                document.documentElement.removeEventListener('mouseup', up);
                selection.style.display = 'none';
            };
            document.documentElement.addEventListener('mousemove', move);
            document.documentElement.addEventListener('mouseup', up);
        },
        onDragover(e) {
            // 
            if (this.isDragSource) {
                // 
                e.dataTransfer.dropEffect = 'none';
                return;
            }
            const isFile = e.dataTransfer.items[0].kind == 'file';
            const isDriveFile = e.dataTransfer.types[0] == 'mk_drive_file';
            const isDriveFolder = e.dataTransfer.types[0] == 'mk_drive_folder';
            if (isFile || isDriveFile || isDriveFolder) {
                e.dataTransfer.dropEffect = e.dataTransfer.effectAllowed == 'all' ? 'copy' : 'move';
            }
            else {
                e.dataTransfer.dropEffect = 'none';
            }
            return false;
        },
        onDragenter(e) {
            if (!this.isDragSource)
                this.draghover = true;
        },
        onDragleave(e) {
            this.draghover = false;
        },
        onDrop(e) {
            this.draghover = false;
            // 
            if (e.dataTransfer.files.length > 0) {
                for (const file of Array.from(e.dataTransfer.files)) {
                    this.upload(file, this.folder);
                }
                return;
            }
            //#region 
            const driveFile = e.dataTransfer.getData('mk_drive_file');
            if (driveFile != null && driveFile != '') {
                const file = JSON.parse(driveFile);
                if (this.files.some(f => f.id == file.id))
                    return;
                this.removeFile(file.id);
                this.$root.api('drive/files/update', {
                    fileId: file.id,
                    folderId: this.folder ? this.folder.id : null
                });
            }
            //#endregion
            //#region 
            const driveFolder = e.dataTransfer.getData('mk_drive_folder');
            if (driveFolder != null && driveFolder != '') {
                const folder = JSON.parse(driveFolder);
                // reject
                if (this.folder && folder.id == this.folder.id)
                    return false;
                if (this.folders.some(f => f.id == folder.id))
                    return false;
                this.removeFolder(folder.id);
                this.$root.api('drive/folders/update', {
                    folderId: folder.id,
                    parentId: this.folder ? this.folder.id : null
                }).then(() => {
                    // noop
                }).catch(err => {
                    switch (err) {
                        case 'detected-circular-definition':
                            this.$root.dialog({
                                title: this.$t('unable-to-process'),
                                text: this.$t('circular-reference-detected')
                            });
                            break;
                        default:
                            this.$root.dialog({
                                type: 'error',
                                text: this.$t('unhandled-error')
                            });
                    }
                });
            }
            //#endregion
        },
        selectLocalFile() {
            this.$refs.fileInput.click();
        },
        urlUpload() {
            this.$root.dialog({
                title: this.$t('url-upload'),
                input: {
                    placeholder: this.$t('url-of-file')
                }
            }).then(({ canceled, result: url }) => {
                if (canceled)
                    return;
                this.$root.api('drive/files/upload_from_url', {
                    url: url,
                    folderId: this.folder ? this.folder.id : undefined
                });
                this.$root.dialog({
                    title: this.$t('url-upload-requested'),
                    text: this.$t('may-take-time')
                });
            });
        },
        createFolder() {
            this.$root.dialog({
                title: this.$t('create-folder'),
                input: {
                    placeholder: this.$t('folder-name')
                }
            }).then(({ canceled, result: name }) => {
                if (canceled)
                    return;
                this.$root.api('drive/folders/create', {
                    name: name,
                    parentId: this.folder ? this.folder.id : undefined
                }).then(folder => {
                    this.addFolder(folder, true);
                });
            });
        },
        onChangeFileInput() {
            for (const file of Array.from(this.$refs.fileInput.files)) {
                this.upload(file, this.folder);
            }
        },
        upload(file, folder) {
            if (folder && typeof folder == 'object')
                folder = folder.id;
            this.$refs.uploader.upload(file, folder);
        },
        chooseFile(file) {
            const isAlreadySelected = this.selectedFiles.some(f => f.id == file.id);
            if (this.multiple) {
                if (isAlreadySelected) {
                    this.selectedFiles = this.selectedFiles.filter(f => f.id != file.id);
                }
                else {
                    this.selectedFiles.push(file);
                }
                this.$emit('change-selection', this.selectedFiles);
            }
            else {
                if (isAlreadySelected) {
                    this.$emit('selected', file);
                }
                else {
                    this.selectedFiles = [file];
                    this.$emit('change-selection', [file]);
                }
            }
        },
        newWindow(folder) {
            if (document.body.clientWidth > 800) {
                this.$root.new(MkDriveWindow, {
                    folder: folder
                });
            }
            else {
                window.open(`${url}/i/drive/folder/${folder.id}`, 'drive_window', 'height=500, width=800');
            }
        },
        move(target) {
            if (target == null) {
                this.goRoot();
                return;
            }
            else if (typeof target == 'object') {
                target = target.id;
            }
            this.fetching = true;
            this.$root.api('drive/folders/show', {
                folderId: target
            }).then(folder => {
                this.folder = folder;
                this.hierarchyFolders = [];
                const dive = folder => {
                    this.hierarchyFolders.unshift(folder);
                    if (folder.parent)
                        dive(folder.parent);
                };
                if (folder.parent)
                    dive(folder.parent);
                this.$emit('open-folder', folder);
                this.fetch();
            });
        },
        addFolder(folder, unshift = false) {
            const current = this.folder ? this.folder.id : null;
            if (current != folder.parentId)
                return;
            if (this.folders.some(f => f.id == folder.id)) {
                const exist = this.folders.map(f => f.id).indexOf(folder.id);
                Vue.set(this.folders, exist, folder);
                return;
            }
            if (unshift) {
                this.folders.unshift(folder);
            }
            else {
                this.folders.push(folder);
            }
        },
        addFile(file, unshift = false) {
            const current = this.folder ? this.folder.id : null;
            if (current != file.folderId)
                return;
            if (this.files.some(f => f.id == file.id)) {
                const exist = this.files.map(f => f.id).indexOf(file.id);
                Vue.set(this.files, exist, file);
                return;
            }
            if (unshift) {
                this.files.unshift(file);
            }
            else {
                this.files.push(file);
            }
        },
        removeFolder(folder) {
            if (typeof folder == 'object')
                folder = folder.id;
            this.folders = this.folders.filter(f => f.id != folder);
        },
        removeFile(file) {
            if (typeof file == 'object')
                file = file.id;
            this.files = this.files.filter(f => f.id != file);
        },
        appendFile(file) {
            this.addFile(file);
        },
        appendFolder(folder) {
            this.addFolder(folder);
        },
        prependFile(file) {
            this.addFile(file, true);
        },
        prependFolder(folder) {
            this.addFolder(folder, true);
        },
        goRoot() {
            // root
            if (this.folder == null)
                return;
            this.folder = null;
            this.hierarchyFolders = [];
            this.$emit('move-root');
            this.fetch();
        },
        fetch() {
            this.folders = [];
            this.files = [];
            this.moreFolders = false;
            this.moreFiles = false;
            this.fetching = true;
            let fetchedFolders = null;
            let fetchedFiles = null;
            const foldersMax = 30;
            const filesMax = 30;
            // 
            this.$root.api('drive/folders', {
                folderId: this.folder ? this.folder.id : null,
                limit: foldersMax + 1
            }).then(folders => {
                if (folders.length == foldersMax + 1) {
                    this.moreFolders = true;
                    folders.pop();
                }
                fetchedFolders = folders;
                complete();
            });
            // 
            this.$root.api('drive/files', {
                folderId: this.folder ? this.folder.id : null,
                type: this.type,
                limit: filesMax + 1
            }).then(files => {
                if (files.length == filesMax + 1) {
                    this.moreFiles = true;
                    files.pop();
                }
                fetchedFiles = files;
                complete();
            });
            let flag = false;
            const complete = () => {
                if (flag) {
                    for (const x of fetchedFolders)
                        this.appendFolder(x);
                    for (const x of fetchedFiles)
                        this.appendFile(x);
                    this.fetching = false;
                }
                else {
                    flag = true;
                }
            };
        },
        fetchMoreFiles() {
            this.fetching = true;
            const max = 30;
            // 
            this.$root.api('drive/files', {
                folderId: this.folder ? this.folder.id : null,
                type: this.type,
                untilId: this.files[this.files.length - 1].id,
                limit: max + 1
            }).then(files => {
                if (files.length == max + 1) {
                    this.moreFiles = true;
                    files.pop();
                }
                else {
                    this.moreFiles = false;
                }
                for (const x of files)
                    this.appendFile(x);
                this.fetching = false;
            });
        }
    }
});
